import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';

export const fetchRepresentatives = createAsyncThunk('representatives/fetchRepresentatives', async () => {
  const response = await axios.get('/api/representatives');
  return response.data;
});

const representativesSlice = createSlice({
  name: 'representatives',
  initialState: {
    representatives: [],
    status: 'idle',
    error: undefined as string | undefined,
  },
  reducers: {},
  extraReducers: (builder) => {
    builder
      .addCase(fetchRepresentatives.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchRepresentatives.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.representatives = action.payload;
      })
      .addCase(fetchRepresentatives.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.error.message;
      });
  },
});

export default representativesSlice.reducer;
